<template lang="html">
  <div class="box">
     <div class="big-box">
        <div class="header-top">
           <div class="header-left">
               <span class="span1">运单管理</span>
               <span class="span-center"> / </span>
               <span class="span2">说明说明说明说明说明</span>
           </div>
           <div class="header-right">
             <router-link to="/WDCD/add-dingdan" class='btn1' tag="button">新建运单</router-link>
             <button class="btn2">EXCEL导出</button>
            </div>
        </div>
        <div class="main">
            <div class="main-top">
              <el-form :inline="true" :model="formInline" class="demo-form-inline">
                    <el-form-item label="订 单 号 ">
                        <el-input v-model="formInline.user" placeholder="请输入订单号" style="width:226px;"></el-input>
                    </el-form-item>

                    <el-form-item label="托 运 日 期">
                       <el-col :span="25">
                           <el-date-picker type="date" placeholder="选择下单区间" v-model="form.date1" style="width: 100%;"></el-date-picker>
                       </el-col>
                    </el-form-item>

                    <el-form-item label="到 站 城 市">
                        <el-select v-model="formInline.region" placeholder="选择" style="width:220px;">
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="收 货 单 位">
                        <el-select v-model="formInline.regione" placeholder="选择区间" style="width:220px;">
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="托运单位">
                        <el-select v-model="formInline.regiont" placeholder="选择" style="width:220px;">
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="司 机 姓 名">
                        <el-select v-model="formInline.regionf" placeholder="选择" style="width:215px;">
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="订 单 状 态">
                        <el-select v-model="formInline.regioni" placeholder="全部" style="width:220px;">
                            <el-option label="区域一" value="shanghai"></el-option>
                            <el-option label="区域二" value="beijing"></el-option>
                        </el-select>
                    </el-form-item>

                    <el-form-item label="司机手机号">
                        <el-input v-model="formInline.usera" placeholder="请输入手机号" style="width:220px;"></el-input>
                    </el-form-item>

                    <el-form-item label="车 牌 号">
                        <el-input v-model="formInline.userb" placeholder="请输入手机号" style="width:226px;"></el-input>
                    </el-form-item>

